<template>
  <div>
    <breadcrumb-box>
    </breadcrumb-box>
    <el-row :gutter="20">
      <el-col :span="17" >
         <el-card class="box-card">
            <div  style="min-height: 550px;">
              <div class="tab-container">
                <div class="Btn" v-on:click="dialogFormVisible = true">
                  创建课题
                </div>
                <el-tabs v-model="activeName" @tab-click="handleClick" class="tab">
                  <el-tab-pane label="我创建的课题" name="first">
                    <el-row :gutter="20">

                      <el-col :span="12" style="" v-for="(item , index) in subjectmines" :key="index">
                        <el-card class="box-card" shadow='hover'>
                          <router-link :to="{ path: '/studio', query: { id: item.id ,fromPage:'Workbench'}}">
                            <div class="card-header">
                              {{item.title}}
                            </div>
                            <div class="card-main">
                              {{item.content}}
                            </div>

                          </router-link>

                          <div class="card-footer flex-space-between">
                            <div class="card-footer-left">
                              <ul>
                                <li><span v-show="item.hide_flag == 1">私有</span> <span style="color: #409EFF;"
                                                                                      v-show="item.hide_flag == 0">公开 </span>
                                </li>
                              </ul>
                              <span> {{item.mtime}}</span>
                            </div>
                            <div class="card-footer-right">
                              <el-tooltip class="item" effect="dark" content="编辑" placement="bottom-start">
                                <el-button type="text">   <span class="edit-outline" v-on:click="editsubjec(item)">


                                        <img src="../../assets/ico/edit.png" height="16" width="16" alt="编辑"/></span></el-button>
                              </el-tooltip>
                              <el-tooltip class="item" effect="dark" content="添加人员" placement="bottom-start">
                                <span class="add-person">


                                        <img src="../../assets/ico/addingpeople.png" v-on:click="addperson(item.id)"
                                            height="16" width="16"
                                            alt="添加人员"/></span>
                              </el-tooltip>
                              <el-tooltip class="item" effect="dark" :content="item.hide_flag == 0 ? '公开': '私有'" placement="bottom-start">
                                <el-switch
                                  class="sw"
                                  v-model="item.hide_flag == 0 ? true: false"
                                  @change="switchsubject(item.hide_flag ,item.id )"
                                >
                                </el-switch>
                              </el-tooltip>

                            </div>


                          </div>


                        </el-card>
                      </el-col>
                    </el-row>

                  </el-tab-pane>
                  <el-tab-pane label="参与的课题" name="second">
                    <el-row :gutter="20">
                      <el-col :span="12" v-for="(item) in subjememberofs" :key="item.id">
                        <el-card class="box-card"  shadow='hover'>
                          <router-link :to="{ path: '/studio', query: { id: item.id ,fromPage:'Workbench'}}">
                            <div class="FLEX card-main-join ">
                              <div class="card-img">
                                <avatar :headIcon = 'item.creator.user_headicon'  :name='item.creator.user_abbr' :bgColor='item.creator.user_backcolor' size="mini"
                                        style="margin: 0 auto; width: 45px; height: 45px;line-height: 45px;"></avatar>
                              </div>
                              <div class="card-txt">
                                <div class="card-header">
                                  {{item.title}}
                                </div>

                              </div>

                            </div>
                            <div class="txt-content">
                              {{item.content}}
                            </div>
                          </router-link>

                          <div class="card-footer card-footer-join">
                            <el-row>
                              <el-col :span="12"  style="border-right: #ddd 1px solid">
                                <div class="card-footer-left card-footer-left-join">
                                  <ul >
                                    <li><span v-show="item.hide_flag == 1">私有</span> <span style="color: #409EFF;"
                                                                                          v-show="item.hide_flag == 0">公开 </span>
                                    </li>
                                  </ul>
                                  <div class="time" style="margin-left: 10px;">
                                    <span> {{item.mtime}}更新</span>
                                  </div>
                                </div>
                              </el-col>
                              <el-col :span="12">
                                <div class="card-footer-right card-footer-right-join">
                                  <router-link :to="{ path: '/studio', query: { id: item.id }}"> 进入工作室</router-link>
                                </div>
                              </el-col>
                            </el-row>
                          </div>
                        </el-card>
                      </el-col>
                    </el-row>
                  </el-tab-pane>
                </el-tabs>
              </div>
            </div>
         </el-card>
      </el-col>
      <el-col :span="7">
          <el-card class="box-card"  >
            <div class="list-box">
              <div class="flex-space-between">
                <div class="h1 " >我的资料库</div>
                <div class="more">
                  <router-link :to="{ path: '/dataList'}"> 全部 >></router-link>
                </div>
              </div>
              <ul v-if="materiallists.length > 0" style="margin-top:10px">
                <router-link class="ellipsis" v-for="(item , index) in materiallists" :key="index" v-show="index < 5"
                :to="'/dataDetail/'+item.id+'?fromPage=Workbench'">
                  <li class="flex-space-between" >
                    <div class="list-title ellipsis">
                      {{item.title}}
                    </div>
                    <div class="list-time">
                      {{item.mtime}}
                    </div>
                  </li>
                </router-link>
              </ul>
              <div class="nulldata" v-else>
                <div class="time nulldata-txt" style="margin-bottom: 70px;">
                  暂无内容，请在课题里添加
                </div>
              </div>
            </div>
          </el-card>
          <el-card class="box-card" >
            <div class="list-box">
              <div class="flex-space-between">
                <div class="h1 " >我的文章</div>
                <div class="more">
                  <router-link :to="{ path: '/articleList'}"> 全部 >></router-link>
                </div>
              </div>
              <ul v-if="articlemines.length > 0" style="margin-top:10px">
                <router-link class="ellipsis" v-for="(item , index) in articlemines" :key="index" v-show="index < 5"
                :to="'/articleDetail/'+item.id+'?fromPage=Workbench'">
                  <li class="flex-space-between" >
                    <div class="list-title ellipsis">
                        {{item.title}}
                    </div>
                    <div class="list-time">
                      {{item.mtime}}
                    </div>
                  </li>
                 </router-link>
              </ul>
              <div class="nulldata" v-else>
                <div class="time nulldata-txt" style="margin-bottom: 70px;">
                  暂无内容，请在课题里添加
                </div>
              </div>
             </div>
          </el-card>
      </el-col>
    </el-row>
    <el-dialog title="创建课题" :visible.sync="dialogFormVisible">
      <el-form :model="subject">
        <el-form-item label="课题名称" :label-width="formLabelWidth">
          <el-input v-model="subject.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="摘要" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="subject.content" :rows="5"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="subjectadd">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="编辑课题" :visible.sync="dialogFormVisible1">

      <el-form :model="editsubject">


        <el-form-item label="课题名称" :label-width="formLabelWidth">
          <el-input v-model="editsubject.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="摘要" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="editsubject.content" :rows="5"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible1 = false">取 消</el-button>
        <el-button type="primary" @click="subjectmodify(editsubject.id)">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="添加人员" :visible.sync="dialogFormVisible2">
      <div>
        <el-input placeholder="输入邮箱或用户名搜索" v-model="key" @keyup.enter.native="infofind">
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input>
      </div>
      <div class="search-result">

        <ul>
          <li class="flex-space-between" v-for="item   in searchuser" :key="item.id" v-show="searchuser.length > 0">
            <div class="user FLEX">
              <avatar :headIcon='item.user_headicon'  :bgColor='item.user_backcolor' :name='item.user_abbr' size="mini"
                      style="margin: 0 auto; width: 45px; height: 45px;line-height: 45px;"></avatar>
              <div class="user-info ">
                <div class='time'>{{item.user_name}}</div>
                <div>{{item.account}}</div>
              </div>
            </div>
            <el-button size="small" @click="subjectaddmember(item.id)">添加</el-button>
          </li>
        </ul>
      </div>
    </el-dialog>
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <span v-if="hide_flag">公开后该课题显示在大厅，确认操作？</span>
      <span  v-else>关闭开关后不能显示在大厅，确定操作？</span>


      <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="setstaus">确 定</el-button>
  </span>
    </el-dialog>

  </div>
</template>
<script>
  import BreadcrumbBox from '@/layout/BreadcrumbBox'
  import Avatar from '@/components/Avatar/index'

  import {
    materialmine,
    articlemine,
    subjectmine,
    subjectadd,
    subjectmodify,
    infofind,
    subjectaddmember,
    subjectclose,
    subjectopen,
    subjectmemberof
  } from '@/api/index'

  export default {
    name: "workbench",
    components: {
      BreadcrumbBox,
      Avatar
    },
    data() {
      return {
        activeName: "first",
        value: true,
        limit: 5,
        page: 1,
        materiallists: [],
        articlemines: [],
        subjectmines: [],
        editsubject: {
          title: '',

          content: ''

        },


        dialogFormVisible: false,
        dialogFormVisible1: false,
        dialogFormVisible2: false,
        centerDialogVisible: false,
        subject: {
          title: '',

          content: ''
        },
        formLabelWidth: '120px',
        key: '',
        searchuser: [],
        sid: '',
        id: this.$store.state.user.user.id,
        hide_flag: 0,
        subjememberofs: []


      };
    },
    mounted() {
      this.materialmine()
      this.articlemine()
      this.subjectmine()


    },
    methods: {
      subjectadd() {
        this.dialogFormVisible = false
        subjectadd({
          title: this.subject.title,
          content: this.subject.content
        }).then((res) => {
          this.subjectmine()
          /* this.subjectmines = res.data.list*/


        })

      },

      subjectmodify(value) {
        this.dialogFormVisible1 = false
        subjectmodify({
          id: value,

          title: this.editsubject.title,
          content: this.editsubject.content
        }).then((res) => {
          this.subjectmine()
          /* this.subjectmines = res.data.list*/


        })

      },
      editsubjec(value) {
        this.dialogFormVisible1 = true

        this.editsubject = value

        console.log('editsubject', this.editsubject)
      },
      addperson(value) {
        this.dialogFormVisible2 = true
        this.sid = value

      },
      subjectaddmember(value) {
        subjectaddmember({
          id: this.sid,
          uid: value


        }).then((res) => {
          if (res.code == 200) {
            this.$message({
              message: '添加成员成功！',
              type: 'success'
            });

          } else {
            this.$message({
              message: '添加成员失败！',
              type: 'error'
            });
          }
          this.dialogFormVisible2 = false


        })

      },

      subjectmine() {
        subjectmine({
          limit: this.limit,
          page: this.page
        }).then((res) => {
          this.subjectmines = res.data.list


        })

      },
      subjectmemberof() {
        subjectmemberof({
          limit: this.limit,
          page: this.page
        }).then((res) => {
          this.subjememberofs = res.data.list


        })

      },

      materialmine() {
        materialmine({
          limit: this.limit,
          page: this.page
        }).then((res) => {
          this.materiallists = res.data.list


        })

      },
      articlemine() {
        articlemine({
          limit: this.limit,
          page: this.page

        }).then((res) => {
          this.articlemines = res.data.list


        })

      },
      infofind() {

        infofind({
          key: this.key,
          limit: this.limit,
          page: this.page

        }).then((res) => {

          this.searchuser = res.data.list


        })

      },
      switchsubject(value1, value2) {
        this.centerDialogVisible = true
        this.hide_flag = value1,
          this.sid = value2


      },
      setstaus() {

        if (this.hide_flag == 0) {
          subjectclose({
            id: this.sid
          }).then((res) => {
            if (res.code == 200) {
              this.$message({
                message: '设为私有成功！',
                type: 'success'
              });
              this.subjectmine()


            }



          })

        } else {
          subjectopen({
            id: this.sid
          }).then((res) => {
            if (res.code == 200) {
              this.$message({
                message: '设为公开成功！',
                type: 'success'
              });


            }
            this.subjectmine()


          })
        }


        this.centerDialogVisible = false


      },


      handleClick(tab, event) {
        if (tab.label == '我创建的课题') {
          this.subjectmine()

        } else if (tab.label == '参与的课题') {
          this.subjectmemberof()

        }

      }
    }
  };
</script>
<style lang="scss" scoped>
  .tab-container {
    position: relative;
  }

  .Btn {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .box-card {
    position: relative;
  }

  .list-title {
    flex: 1
  }

  .card-main {
    margin: 15px 0px 50px;
    word-break: break-all;
    height: 54px;
    color: #989898;
    line-height: 18px;
    text-overflow: ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp: 3; /** 显示的行数 **/
    overflow: hidden; /** 隐藏超出的内容 **/
    font-size: 14px;
  }



  .card-txt {
    -webkit-flex: 1;
    flex: 1;
    margin-left: 10px;
    line-height: 45px;


  }

  .list-box {
    min-height: 280px;
  }

  .list-box /deep/ .flex-space-between:last-child {
    border:0
  }

  .txt-content {
    word-break: break-all;
    height: 55px;
    color: #989898;
    line-height: 18px;
    text-overflow: ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp: 3; /** 显示的行数 **/
    overflow: hidden;
    font-size: 12px;
    margin: 0px 0px 50px 55px;

  }

  .card-img {
    width: 45px;
    height: 45px;
    border-radius: 100%;

  }

  .card-footer {
    border-top: 1px solid #EBEEF5;

    padding-top: 15px;

    background: #eee;
    position: absolute;
    height: 42px;
    left: 0px;
    bottom: 0px;
    width: 100%;

  }

  .card-footer-join {

    padding: 5px 0px;

  }

  .card-footer-left ul {
    padding: 0px 0px 0px 35px !important;
    margin: 0px;
    display: inline-block

  }

  .card-footer-left ul li {
    padding: 0px;
  }

  .card-footer-left span {
    font-size: 12px;
    color: #CCCCCC;
    margin-left: 10px;
  }

  .card-footer-right .add-person, .card-footer-right .edit-outline {
    display: inline-block;
    width: 20px;
    height: 20px;

    /*cursor: pointer;*/
  }

  .add-person {
    margin-right: 40px;
  }

  .card-footer-right {

    margin-right: 25px;
  }



  .card-footer-left-join ul li {
    font-size: 12px;
   /* padding: 7px 0px;*/

  }

  .card-footer-right-join {
    color: rgba(0, 0, 0, 0.427450980392157);
    text-align: center;
    line-height: 42px;
    position: relative;
  }

  .sw {
    position: absolute;
    right: 20px;
    top: 14px;

  }

  .search-result {
    margin-top: 60px;
  }

  .search-result li {
    padding: 20px 0px;
  }

  .user-info {
    margin-left: 10px;
  }

  .nulldata-txt {
    text-align: center;
    margin-top: 60px;
  }


</style>
<style scoped>
  .tab-container >>> .el-tabs__nav-wrap::after {
    display: none;
  }
  .card-footer-right >>> .el-button{
    padding:0px 7px;
  }
 .tab>>> .el-card{
    border: 2px solid #EBEEF5;
  }


  .el-card.is-hover-shadow:focus, .el-card.is-hover-shadow:hover{
    border: 2px solid rgba(64,158,255,.2) ;

     -webkit-box-shadow: 0 2px 12px 0 rgba(64,158,255,.4);
     box-shadow: 0 2px 12px 0 rgba(64,158,255,.4);


  }

  /*.tab >>> .el-card {
  }*/
</style>

